import ICourse from "../types/ICourse";
import http from "../utils/http-common";

const getAll = (page:number, size:number): Promise<any> => {
  return http.get(`/course?page=${page}&size=${size}`);
};

const get = (uuid:any): Promise<any> => {
  return http.get(`/course/${uuid}`);
};

// event : Y 인 것만 가져오기
const getAllByEvent = (page:number, size:number): Promise<any> => {
  return http.get(`/course/event?page=${page}&size=${size}`);
};

const create = (uploadCourse:ICourse, blobFile:any): Promise<any> => {
  console.log("uploadCourse() parameter ; ", uploadCourse);

  let formData = new FormData();

  formData.append("code", uploadCourse.code);
  formData.append("title", uploadCourse.title);
  formData.append("content", uploadCourse.content);
  formData.append("eventYn", uploadCourse.eventYn);
  formData.append("eventTitle", uploadCourse.eventTitle);
  formData.append("eventContent", uploadCourse.eventContent);
  formData.append("blobFile", blobFile);

  return http.post("/course", formData, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};

const update = (uploadCourse:ICourse, blobFile:any):Promise<any> => {
  console.log("update() parameter ; ", uploadCourse);

  let formData = new FormData();

  formData.append("code", uploadCourse.code);
  formData.append("title", uploadCourse.title);
  formData.append("content", uploadCourse.content);
  formData.append("eventYn", uploadCourse.eventYn);
  formData.append("eventTitle", uploadCourse.eventTitle);
  formData.append("eventContent", uploadCourse.eventContent);
  formData.append("blobFile", blobFile);

  return http.put(`/course/${uploadCourse.uuid}`, formData, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};

const deleteCourse = (uuid:any): Promise<any> => {
  return http.delete(`/course/deletion/${uuid}`);
};

const CourseService = {
  getAll,
  get,
  getAllByEvent,
  create,
  update,
  deleteCourse,
};

export default CourseService;