import ICode from "../types/ICode";
import http from "../utils/http-common";

// addCourseView selectbox에 사용됨
const getAll = () => {
  return http.get(`/code/all`);
};

const getAllPage = (searchKeyword:string, page:number, size:number) => {
  console.log(searchKeyword);
  console.log(page);
  console.log(size);
  return http.get(
    `/code?searchKeyword=${searchKeyword}&page=${page}&size=${size}`
  );
};

const create = (data:ICode) => {
  return http.post("/code", data);
};

const deleteCode = (codeId:number) => {
  return http.delete(`/code/${codeId}`);
};

const CodeService = {
  getAll,
  getAllPage,
  create,
  deleteCode,
}
export default CodeService;
