// AddCode.js
import React from "react";
import { useEffect, useState } from "react";
import initMain from "../../../assets/js/main";
import CodeService from "../../../services/CodeService";

function AddCode() {
  useEffect(() => {
    initMain();
  }, []);

  const initialCodeState = {
    codeId: "",
    category: "LARGE",
    title: "",
  };

  const [code, setCode] = useState(initialCodeState);

  const handleInputChange = (event:any) => {
    const { name, value } = event.target;
    setCode({ ...code, [name]: value });
  };

  const saveCode = () => {
    let data = {
      codeId: code.codeId,
      category: code.category,
      title: code.title,
    };

    CodeService.create(data)
      .then((response:any) => {
        setCode({
          codeId: response.data.codeId,
          category: response.data.category,
          title: response.data.title,
        });
        console.log(response.data);
        alert("새로운 코드가 저장되었습니다. ");
      })
      .catch(e => {
        console.log(e);
      });
  };

  return (
    <main id="main" data-aos="fade-in">
      {/* <!-- ======= Breadcrumbs ======= --> */}
      <div className="breadcrumbs">
        <div className="container">
          <h2>Add Course</h2>
          <p>Course 과정명 / 내용 / 이미지 업로드</p>
        </div>
      </div>
      {/* <!-- End Breadcrumbs --> */}

      {/* <!-- row 시작 --> */}
      {/* <!-- ======= Courses Section ======= --> */}
      <section id="courses" className="courses">
        <div className="container" data-aos="fade-up">
          <div className="row">
            <div className="col-lg-8">
              <div className="mb-3">
                <label htmlFor="codeId" className="form-label">
                  과정 코드
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="codeId"
                  required
                  placeholder="예) L001"
                  value={code.codeId || ""}
                  name="codeId"
                  onChange={handleInputChange}
                />
              </div>

              <div className="mb-3">
                <label htmlFor="category" className="form-label">
                  코드 분류
                </label>
                <select
                  className="form-select"
                  aria-label="Default select example"
                  value={code.category || ""}
                  name="category"
                  onChange={handleInputChange}

                >
                  <option value="LARGE">
                    LARGE
                  </option>
                  <option value="MIDDLE">
                    MIDDLE
                  </option>
                  <option value="SMALL">
                    SMALL
                  </option>
                </select>
              </div>

              <div className="mb-3">
                <label htmlFor="title" className="form-label">
                  과정명
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  required
                  value={code.title || ""}
                  name="title"
                  onChange={handleInputChange}

                />
              </div>

              <div className="mb-3">
                <button
                  onClick={saveCode}
                  className="btn btn-success btn-sm float-left"
                >
                  Submit
                </button>
              </div>
            </div>
          </div>
        </div>
        {/* <!-- row 끝 --> */}
      </section>
    </main>
  );
}

export default AddCode;
