// EditCourse.js
import React from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import initMain from "../../assets/js/main";
import CodeService from "../../services/CodeService";
import CourseService from "../../services/CourseService";
import ICourse from "../../types/ICourse";
import ICode from "../../types/ICode";

function EditCourse() {
  const { uuid } = useParams();
  const navigate = useNavigate();

  const initialCourse = {
    uuid: uuid,
    code: "L001",
    title: "",
    content: "",
    eventYn: "Y",
    eventTitle: "",
    eventContent: "",
  };

  const [selectedFiles, setSelectedFiles] = useState<FileList>(); // 현재 선택한 파일을 저장할 변수
  const [uploadCourse, setUploadCourse] = useState<ICourse>(initialCourse); // upload 대상 객체
  const [codes, setCodes] = useState<Array<ICode>>([]); // code 번호 가져와서 저장할 배열변수

  const [message, setMessage] = useState("");

  const retrieveCode = () => {
    CodeService.getAll()
      .then((response) => {
        console.log(response.data);
        setCodes(response.data);
      })
      .catch((err) => {
        setMessage("DB에서 코드를 가져올 수 없습니다. " + err);
      });
  };

  const getCourse = (uuid: any) => {
    CourseService.get(uuid)
      .then((response: any) => {
        setUploadCourse(response.data);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  useEffect(() => {
    if (uuid) {
      initMain();
      getCourse(uuid);
      retrieveCode();
    }
  }, [uuid]);

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLSelectElement> | React.ChangeEvent<HTMLTextAreaElement>) => {
    const { name, value } = event.target;
    setUploadCourse({ ...uploadCourse, [name]: value });
  };

  // 파일 선택상자 이미지 선택시 실행되는 함수
  const selectFile = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedFiles(event.target.files as FileList); // 타입 단언
  };

  const updateCourse = () => {
    let currentFile = selectedFiles?.[0];

    CourseService.update(uploadCourse, currentFile)
      .then((response: any) => {
        alert("수정이 정상적으로 성공했습니다.");
        navigate("/course");
      })
      .catch(() => {
        setMessage("Could not upload the file!");
      });

    setSelectedFiles(undefined);
  };

  return (
    <main id="main" data-aos="fade-in">
      {/* <!-- ======= Breadcrumbs ======= --> */}
      <div className="breadcrumbs">
        <div className="container">
          <h2>Edit Course</h2>
          <p>Course 과정명 / 내용 / 이미지 업로드</p>
        </div>
      </div>
      {/* <!-- End Breadcrumbs --> */}

      {/* <!-- TODO: v-if="uploadCourse" 로 데이터가 오고나서 DOM 이 실행되도록 하는 로직이 필요 --> */}
      {/* <!-- row 시작 --> */}
      {/* <!-- ======= Courses Section ======= --> */}

      {uploadCourse && (
        <section id="courses" className="courses">
          <div className="container" data-aos="fade-up">
            <div className="row">
              <div className="col-lg-8">
                <div className="mb-3">
                  <label htmlFor="type" className="form-label">
                    과목코드
                  </label>
                  <select
                    className="form-select"
                    id="code"
                    aria-label="Default select example"
                    name="code"
                    value={uploadCourse.code}
                    onChange={handleInputChange}
                  >
                    {codes &&
                      codes.map((code) => (
                        <option key={code.codeId} value={code.codeId}>
                          {code.title}
                        </option>
                      ))}
                  </select>
                </div>

                <div className="mb-3">
                  <label htmlFor="name" className="form-label">
                    과정명
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="title"
                    required
                    name="title"
                    value={uploadCourse.title}
                    onChange={handleInputChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="content" className="form-label">
                    과정내용
                  </label>
                  <textarea
                    className="form-control"
                    id="content"
                    required
                    rows={4}
                    name="content"
                    value={uploadCourse.content}
                    onChange={handleInputChange}
                  ></textarea>
                </div>

                <div className="d-flex mb-3">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="radio"
                      id="eventYn"
                      value="Y"
                      name="eventYn"
                      checked={uploadCourse.eventYn === "Y"}
                      onChange={handleInputChange}
                    />
                    <label className="form-check-label" htmlFor="eventYn">
                      이벤트 있음
                    </label>
                  </div>
                  <div className="form-check ms-2">
                    <input
                      className="form-check-input"
                      type="radio"
                      id="eventYn"
                      value="N"
                      name="eventYn"
                      checked={uploadCourse.eventYn === "N"}
                      onChange={handleInputChange}
                    />
                    <label className="form-check-label" htmlFor="eventYn">
                      이벤트 없음
                    </label>
                  </div>
                </div>

                {uploadCourse.eventYn === "Y" && (
                  <div className="mb-3">
                    <label htmlFor="eventTitle" className="form-label">
                      이벤트명
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="eventTitle"
                      required
                      name="eventTitle"
                      value={uploadCourse.eventTitle||''}
                      onChange={handleInputChange}
                    />
                  </div>
                )}

                {uploadCourse?.eventYn === "Y" && (
                  <div className="mb-3">
                    <label htmlFor="eventContent" className="form-label">
                      이벤트내용
                    </label>
                    <textarea
                      className="form-control"
                      id="eventContent"
                      required
                      rows={4}
                      name="eventContent"
                      value={uploadCourse.eventContent||''}
                      onChange={handleInputChange}
                    ></textarea>
                  </div>
                )}

                <div className="mb-3">
                  <label className="btn btn-default p-0">
                    {/* <!-- 파일 선택상자 --> */}
                    <input type="file" accept="image/*" onChange={selectFile} />
                  </label>
                </div>

                <div className="mb-3">
                  {/* <!-- 서버에 insert 문 호출 --> */}
                  <button
                    className="btn btn-success btn-sm float-left"
                    disabled={!selectedFiles}
                    onClick={updateCourse}
                  >
                    Edit
                  </button>
                </div>

                {/* <!-- 서버 에러 메세지가 있을경우 아래 출력 --> */}
                {message && (
                  <div className="alert alert-secondary" role="alert">
                    {message}
                  </div>
                )}
              </div>
            </div>
          </div>
          {/* <!-- row 끝 --> */}
        </section>
      )}
    </main>
  );
}

export default EditCourse;
