import React from "react";
import { useEffect, useState } from "react";
import initMain from "../../assets/js/main";

import Pagination from "@mui/material/Pagination";
import ICourse from "../../types/ICourse";
import CourseService from "../../services/CourseService";

function CourseCom(props:any) {
  useEffect(() => {
    initMain();
  }, []);

  const [course, setCourse] = useState<Array<ICourse>>([]);

  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(0);
  const [pageSize, setPageSize] = useState<number>(props.pageProps);

  const pageSizes:Array<number> = [3, 6, 9];

  useEffect(() => {
    retrieveCourse();
  }, [page, pageSize]);

  const retrieveCourse = () => {
    CourseService.getAll(page - 1, pageSize)
      .then((response:any) => {
        const { course, totalPages } = response.data;

        setCourse(course);
        setCount(totalPages);

        console.log(response.data);
      })
      .catch((e:Error) => {
        console.log(e);
      });
  };

  const deleteCourse = (id:any) => {
    CourseService.deleteCourse(id)
      .then((response:any) => {
        console.log(response);

        let message = "정상적으로 삭제되었습니다. ";
        alert(message);

        //  삭제 후 재조회
        retrieveCourse();
      })
      .catch((e:Error) => {
        console.log(e);

        let message = "삭제 시 에러가 발생했습니다. " + e.message;
        alert(message);
      });
  };

  const handlePageChange = (event:React.ChangeEvent<unknown>, value:number) => {
    setPage(value);
  };

  const handlePageSizeChange = (event:React.ChangeEvent<HTMLSelectElement>) => {
    setPageSize(Number(event.target.value));
    setPage(1);
  };

  return (
    <div className="row" data-aos="zoom-in" data-aos-delay="100">
      {/* <!--    Todo : page 바 시작 --> */}
      <div className="offset-1 col-lg-9 col-md-12 d-flex justify-content-between align-items-stretch">
        {/* select box 시작 */}
        {props.isVisible && (
          <div className="d-flex align-items-center">
            Items per Page:
            <select value={pageSize} onChange={handlePageSizeChange}>
              {pageSizes &&
                pageSizes.map((size) => (
                  <option key={size} value={size}>
                    {size}
                  </option>
                ))}
            </select>
          </div>
        )}
        {/* select box 끝 */}

        {/* <!--    Todo : page 바 시작 --> */}
        {props.isVisible && (
          <div className="d-flex align-items-center">
            <Pagination
              className="my-3"
              count={count}
              page={page}
              siblingCount={1}
              boundaryCount={1}
              variant="outlined"
              shape="rounded"
              onChange={handlePageChange}
            />
          </div>
        )}
        {/* <!--    Todo : page 바 끝 --> */}
      </div>
      {/* <!--    Todo : page 바 끝 --> */}

      {/* <!-- Start Course Item--> */}
      <div className="row">
        {course &&
          course.map((data, index) => (
            <div
              className="course-item ms-5 col-lg-3 col-md-3 mt-5"
              key={index}
            >
              <img src={data.fileUrl} className="img-fluid" alt="..." />
              <div className="course-content">
                <div className="d-flex justify-content-between align-items-center mb-3">
                  <h4>Frontend : Web Classic&nbsp;</h4>
                  <span>
                    {data.eventYn === "Y" && (
                      <a href="#" className="ps-2">
                        🎁
                      </a>
                    )}

                    <a
                      href={"/edit-course/" + data.uuid}
                      className="ps-2"
                      style={{ color: "inherit" }}
                    >
                      <i className="fas fas fa-edit" />
                    </a>
                    <a
                      href="#"
                      className="ps-2"
                      style={{ color: "inherit" }}
                      onClick={() => deleteCourse(data.uuid)}
                    >
                      <i className="fas fa-trash" />
                    </a>
                  </span>
                </div>

                <h3>
                  {/* <a href={`/course/${data.id}`}>{data.title}</a> */}
                  {data.title}
                </h3>
                {/* TODO: 리액트에서 태그가 포함된 문자열을 화면에 html 효과 주어서 출력하기 */}
                <p dangerouslySetInnerHTML={{ __html: data.content }}></p>
                <div className="trainer d-flex justify-content-between align-items-center">
                  <div className="trainer-profile d-flex align-items-center">
                    <img
                      src={require("../../assets/img/trainers/my-photo-1.png")}
                      className="img-fluid"
                      alt=""
                    />
                    <span>Kang TaeGyung</span>
                  </div>
                  <div className="trainer-rank d-flex align-items-center">
                    <i className="bx bx-user"></i>&nbsp;50 &nbsp;&nbsp;
                    <i className="bx bx-heart"></i>&nbsp;65
                  </div>
                </div>
              </div>
            </div>
          ))}
      </div>
      {/* <!-- End Course Item--> */}
    </div>
  );
}

export default CourseCom;
