// Code.js
import React from "react";
import { useEffect, useState } from "react";
import initMain from "../../../assets/js/main";
import Pagination from '@mui/material/Pagination';
import CodeService from "../../../services/CodeService";
import ICode from "../../../types/ICode";


function Code() {
  useEffect(() => {
    initMain();
  }, []);

  const [code, setCode] = useState<Array<ICode>>([]);
  const [searchKeyword, setSearchKeyword] = useState<string>("");

  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(0);
  const [pageSize, setPageSize] = useState<number>(3);

  const pageSizes: Array<number> = [3, 6, 9];

  useEffect(() => {
    retrieveCode();
  }, [page, pageSize]);

  const retrieveCode = () => {
    CodeService.getAllPage(searchKeyword, page - 1, pageSize)
      .then((response) => {
        const { code, totalPages } = response.data;

        setCode(code);
        setCount(totalPages);

        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const onChangeSearchKeyword = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchKeyword(e.target.value);
  };

  const deleteCode = (id:any) => {
    CodeService.deleteCode(id)
      .then((response) => {
        console.log(response);

        let message = "정상적으로 삭제되었습니다. ";
        alert(message);

        //  삭제 후 재조회
        retrieveCode();
      })
      .catch((e) => {
        console.log(e);

        let message = "삭제 시 에러가 발생했습니다. " + e.message;
        alert(message);
      });
  };

  const handlePageChange = (event:React.ChangeEvent<unknown>, value:number) => {
    setPage(value);
  };

  const handlePageSizeChange = (event:React.ChangeEvent<HTMLSelectElement>) => {
    setPageSize(Number(event.target.value));
    setPage(1);
  };

  return (
    <div>
      {/* <!-- ======= Breadcrumbs ======= --> */}
      <div className="breadcrumbs">
        <div className="container">
          <h2>View Code</h2>
          <p>Code 보기 / 삭제하기</p>
        </div>
      </div>
      {/* <!-- End Breadcrumbs --> */}

      {/* <!-- row 시작 --> */}
      {/* <!-- ======= Codes Section ======= --> */}
      <section id="code" className="code">
        <div className="container" data-aos="fade-up">
          {/* <!-- search 관련 div 시작 --> */}
          <div className="col-md-8 offset-2">
            <div className="input-group mb-3">
              {/* <!--            Todo : 수정 시작 --> */}
              <input
                type="text"
                className="form-control"
                placeholder="제목 검색"
                value={searchKeyword}
                onChange={onChangeSearchKeyword}
              />
              <div className="input-group-append">
                <button
                  className="btn btn-outline-secondary"
                  type="button"
                  onClick={retrieveCode}
                >
                  Search
                </button>
              </div>
              {/* <!--            Todo : 수정 끝 --> */}
            </div>
          </div>
          {/* <!-- search 관련 div 끝 --> */}

          {/* <!--    Todo : page 바 시작 --> */}
          <div className="col-lg-4 col-md-6 d-flex justify-content-between align-items-stretch mb-2">
            <div>
              Items per Page:
              <select value={pageSize} onChange={handlePageSizeChange}>
                {pageSizes.map((size) => (
                  <option key={size} value={size}>
                    {size}
                  </option>
                ))}
              </select>
            </div>
          </div>
          {/* <!--    Todo : page 바 끝 --> */}

          <div className="row">
            <div className="col-lg-12">
              <table className="table table-hover">
                <thead className="table-success">
                  <tr>
                    <th scope="col">코드 ID</th>
                    <th scope="col">분야</th>
                    <th scope="col">제목</th>
                    <th scope="col">삭제</th>
                  </tr>
                </thead>
                <tbody className="table-striped">
                  {code.map((data) => (
                    <tr key={data.codeId}>
                      <td>{data.codeId}</td>
                      <td>{data.category}</td>
                      <td>{data.title}</td>
                      <td>
                        <a onClick={() => deleteCode(data.codeId)}>
                          <i className="fas fa-trash" />
                        </a>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          {/* <!--    Todo : page 바 시작 --> */}
          <div>
            <Pagination
              className="my-3"
              count={count}
              page={page}
              siblingCount={1}
              boundaryCount={1}
              variant="outlined"
              shape="rounded"
              onChange={handlePageChange}
            />
          </div>
          {/* <!--    Todo : page 바 끝 --> */}
        </div>
      </section>
      {/* <!-- row 끝 --> */}
    </div>
  );
}

export default Code;
