import React from "react";

import "./assets/css/style.css";
// font awesome 라이브러리 설치
import "@fortawesome/fontawesome-free/js/all.js";

import HeaderCom from "./components/common/HeaderCom";
import { Route, Routes } from "react-router-dom";
import Home from "./pages/Home";
import About from "./pages/About";
import Contact from "./pages/Contact";
import Event from "./pages/Event";
import Pricing from "./pages/Pricing";
import TrainerCom from "./components/TrainerCom";
import AddCourse from "./pages/admin/AddCourse";
import Course from "./pages/course/Course";
import EditCourse from "./pages/course/EditCourse";
import Code from "./pages/admin/code/Code";
import AddCode from "./pages/admin/code/AddCode";
import NotFound from "./components/common/NotFound";
import FooterCom from "./components/common/FooterCom";

function App() {
  return (
    <div className="App">
      <HeaderCom />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/home" element={<Home />} />
        <Route path="*" element={<NotFound />} />
        
        <Route path="/about" element={<About />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/event" element={<Event />} />
        <Route path="/pricing" element={<Pricing />} />
        <Route path="/trainer" element={<TrainerCom />} />
        <Route path="/add-course" element={<AddCourse />} />
        <Route path="/course" element={<Course />} />
        <Route path="/edit-course/:uuid" element={<EditCourse />} />
        <Route path="/code" element={<Code />} />
        <Route path="/add-code" element={<AddCode />} />

      </Routes>
      <FooterCom />
    </div>
  );
}

export default App;
