// Event.js
import React from "react";
import { useEffect, useState } from "react";
import initMain from "../assets/js/main";
import Pagination from '@mui/material/Pagination';
import CourseService from "../services/CourseService";
import ICourse from "../types/ICourse";

function Event() {
  const [course, setCourse] = useState<Array<ICourse>>([]);

  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(0);
  const [pageSize, setPageSize] = useState<number>(2);

  const pageSizes: Array<number> = [3, 6, 9];

  useEffect(() => {
    initMain();
    retrieveEvent();
  }, [page, pageSize]);

  const retrieveEvent = () => {
    CourseService.getAllByEvent(page - 1, pageSize)
      .then((response:any) => {
        const { course, totalPages } = response.data;

        setCourse(course);
        setCount(totalPages);

        console.log(response.data);
      })
      .catch((e:Error) => {
        console.log(e);
      });
  };

  const handlePageChange = (event:React.ChangeEvent<unknown>, value:number) => {
    setPage(value);
  };

  const handlePageSizeChange = (event:React.ChangeEvent<HTMLSelectElement>) => {
    setPageSize(Number(event.target.value));
    setPage(1);
  };

  return (
    <main id="main">
      {/* <!-- ======= Breadcrumbs ======= --> */}
      <div className="breadcrumbs" data-aos="fade-in">
        <div className="container">
          <h2>이벤트</h2>
          <p>현재 이벤트 중인 강좌 소개</p>
        </div>
      </div>
      {/* <!-- End Breadcrumbs --> */}

      {/* <!-- ======= Events Section ======= --> */}
      <section id="events" className="events">
        <div className="container" data-aos="zoom-in">
          <div className="row">
            {/* <!--    Todo : page 바 시작 --> */}
            <div className="col-lg-11 col-md-12 d-flex justify-content-between align-items-stretch">
              <div>
                Items per Page:
                <select value={pageSize} onChange={handlePageSizeChange}>
                  {pageSize &&
                    pageSizes.map((size) => (
                      <option key={size} value={size}>
                        {size}
                      </option>
                    ))}
                </select>
              </div>

              {/* <!--    Todo : page 바 시작 --> */}
              <div>
                <Pagination
                  className="my-3"
                  count={count}
                  page={page}
                  siblingCount={1}
                  boundaryCount={1}
                  variant="outlined"
                  shape="rounded"
                  onChange={handlePageChange}
                />
              </div>
              {/* <!--    Todo : page 바 끝 --> */}
            </div>
            {/* <!--    Todo : page 바 끝 --> */}

            <div className="col-md-6 d-flex align-items-stretch">
              {course &&
                course.map((data, index) => (
                  <div className="card" key={index}>
                    <div className="card-img">
                      <img src={data.fileUrl} alt="..." />
                    </div>
                    <div className="card-body">
                      <h5 className="card-title">
                        {/* <Link to={`/course/${data.id}`}>{data.title}</Link> */}
                        {data.title}
                      </h5>
                      <p className="fst-italic text-center">{data.title}</p>
                      <p className="card-text" v-html="data.content"></p>
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
      </section>
      {/* <!-- End Events Section --> */}
    </main>
  );
}

export default Event;
